$mobile-width:"600px";
$smallScreen-width:"900px";
$tableScreen-width:"1200px";
@mixin tablet{
    @media (max-width:#{$tableScreen-width}) {
        @content
    }
};
@mixin smallScreen{
    @media (max-width:#{$smallScreen-width}) {
        @content
    }
};
@mixin mobile{
    @media (max-width:#{$mobile-width}) {
        @content
    }
};